html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}


/* Colours */

:root {
  --primary-colour: #ff9595;
  --secondary-colour: #342b38;
  --tertiary-colour: #80bdab;
  --quaternary-colour: #bbf1c8;
  --test-colour: #fea5a5;
}

/* Global Styles / Resets */

.wrapper {
  max-width: 1400px;
  width: 85%;
  margin: 0 auto;
}

body, h1, h2, p, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ul {
  list-style: none;
}

/* Typography ----> */

html {
  font-size: 62.5%;
}

body {
  font-family: 'Nunito', sans-serif;
  font-size: 2rem;
  color: #000;
}

h1 {
  padding: 40px 0 0 0;
  font-family: 'Sniglet', cursive;
  font-size: 6.5rem;
  text-transform: capitalize;
}

h2 {
  padding-top: 10px;
  font-size: 3rem;
}

/* Background */

body {
  background: #FE91CA;
}

.backgroundContainer {
  width: 85%;
  margin: 85px auto;
  height: 800px;
  background: #D3DBFF;
  border-radius: 30px;
}

/* Header */
header {
  padding-bottom: 40px;
}

/* Main ----> */
form {
  width: 90%;
  height: 300px;
  margin: 0 auto;
  padding: 20px;

  background: #FFE0F7;
  border-radius: 20px;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
form input[type=text] {
  height: 200px;
  width: 500px;
}

.cardContainer {
  width: 200px;
  height: 400px;
  line-height: 1.75;
}